* {
  box-sizing: border-box;
  max-width: 100vw;
  font-family: "Netflix Sans" !important;
}

.App {
  text-align: center;
  /* background-color: #ececec; */
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  /* color: rgb(29, 29, 29); */
}

h1.home-profile-description {
  margin-top: 0px;
}

.home-profile-description {
  margin-bottom: 45.614px;
}

p.home-profile-description {
  font-size: 30px;
}

p.home-liked-stuff-description {
  margin-bottom: 72px;
  font-size: 30px;
}

.container-home-media-cards {
  display: flex !important;
  gap: 18px;
  flex-wrap: wrap;
  justify-content: center !important;
}

.home-footer {
  font-size: 12px;
  margin-top: 48px;
}

.card-component {
  box-shadow: none !important;
  cursor: pointer;
  flex-wrap: wrap !important;
}

a {
  text-decoration: none;
}

.container-home-page {
  width: 1200px !important;
}

.images-carousel-element {
  justify-content: center;
  place-content: center;
}

.nav-bar button {
  font-weight: bolder;
}

p b {
  color: #90caf9;
}

